import React, { Component } from 'react'
import "./Option.scss";

interface IOptionProps {
    option: string;
    price: string;
    desc: string[];
    select: (val) => void; 
    submit: () => void;
}
export default class Option extends Component<IOptionProps, {}> {
  render() {
      const { option, price, desc, select, submit } = this.props;

      const listItems = desc.map((d) => <li>{d}</li>);

    return (
      <div className="option">
        <div className="option-header">
            <h3>{option}</h3>
        </div>
        <div className="option-body">
            <p>{price} kr.</p>
            <ul>
                {listItems}
            </ul>
            <button onClick={() => {select(option); submit()}}>Select</button>
        </div>
      </div>
    )
  }
}
